@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;1,100;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.add-user-form {
  margin-left: 300px;
}
.material-icons {
  color: white;
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 42px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
}

.users-list {
  margin-left: 300px;
  margin-top: 50px;
  margin-bottom: 100px;
  height: 100%;
  width: 75vw;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  flex-wrap: wrap-reverse;
}

.user-photo {
  border-radius: 100px;
  max-height: 100px;
  max-width: 100px;
  padding: 10px;
  margin: -30px 0px 5px 0px;
  box-shadow: 0 0 0 0.25rem rgb(13, 253, 161 / 25%);
  border-color: rgba(129, 235, 129, 0.589);
}
.author {
  display: flex;
  width: 100%;
  align-items: flex-start;
}
.author p {
  margin: 10px 0 0 20px;
}
.name {
  font-weight: bold;
}
.autor-information {
  color: white;
}
.info {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.loader {
  margin-left: 200px;
}

.posts-list {
  margin-top: 30px;
  margin-bottom: 100px;
  margin-left: 300px;
  color: white;
  display: flex;
  flex-direction: column-reverse;
}
