.author {
  font-size: 120%;
  display: flex;
  width: 500px;
  height: 100%;
  align-items: center;
  justify-content: space-around;
}

.author img {
  border-radius: 100px;
  max-height: 80px;
  max-width: 80px;
  margin-left: 5px;
}
.author p {
  margin: 0px 0 0 15px;
}
.name {
  font-weight: bold;
}
.autor-information {
  color: #ffffffa1;
}
.content {
  display: flex;
  flex-direction: column;
  margin-left: 70px;
  text-align: left;
  font-size: 120%;
}
.content-text {
  margin-top: 0px;
  margin-bottom: 10px;
}
.content img {
  width: 500px;
  height: 300px;
  margin-left: -30px;
  margin-right: 20px;
  border-radius: 20px;
}
.badges {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin: 10px 10px 10px 10px;
  color: #ffffffa1;
}
.badges :hover {
  color: white;
}
.badges :active {
  color: red;
}
.badg {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 40px;
}
.post-form {
  margin-left: 300px;
}
.select {
  width: 70%;
}

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 100vw;
  min-height: 100vh;
  background-color: rgba(209, 206, 206, 0.473);
}
* {
  margin: 0;
}
.nav-panel {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(rgba(5, 5, 5, 0.63), rgba(0, 0, 0, 0.5)),
    url(https://www.rabstol.net/uploads/gallery/main/633/rabstol_net_canada_31.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100%;
  width: 17vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-link {
  color: #61dafb;
  text-decoration: none;
  font-size: 1.2em;
  font-weight: 300px;
  margin-top: 10%;
  margin-bottom: 10%;
}
.app-link :hover {
  cursor: pointer;
}
.active-link {
  color: white;
  font-weight: 500;
}
.alert {
  margin-left: 100px;
  margin-bottom: -100px;
  width: 400px;
  height: 150px;
}

